<template>
  <div id = "signin">
    <p class="msg">{{errMsg}}</p>
    <form @submit.prevent="submit">
      <table>
        <tr>
          <td>
            <input type="text" v-model="user.username" class="username" name="username" placeholder="用户名" @blur="check">
          </td>
        </tr>

        <tr>
          <td>
            <input type="password" v-model="user.password" class="password" name="password" placeholder="密码" @blur="check">
          </td>
        </tr>
      </table>
        <input class="btn" type="submit" value="提交">
    </form> 
  </div>
</template>

<script>
export default {
  name: 'signin',
  data() {
    return {
      user: {
        username: '',
        password: ''
      },
      errMsg: ''
    }
  },
  methods: {
    submit: function() {
      if(!this.check()) {
        return;
      } else{
        var formData = JSON.stringify(this.user);
        this.$http.post('/user/signin', formData)
        .then(res => {
            if(res.data == '用户不存在' || res.data == '密码错误'){
              this.errMsg = res.data;
            }else if (document.cookie.length > 0) {
              this.$router.push('/movies');
            }
          })
        .catch(e => {
          console.log("服务器异常！");
          console.log(e)
        })
      }
      
    },
    check: function() {
      if (document.querySelector('.username').value === '' || document.querySelector('.password').value === '') {
          this.errMsg = '用户名/密码不能为空';
          console.log(this.errMsg);
          return false;
      } else {
        this.errMsg = '';
        return true;
      }
    }
  }
}
</script>

<style scoped>
  form {
    margin: 2em;
    display: block;
  }

  table {
    width: 30%;
    margin: 0 auto 0 auto;
  }

  label {
    text-align: right;
  }
  .btn {
    display: inline-block;
    margin: 2em;
    width: 6%;
  }
  .msg {
    color: red;
    margin-bottom: 0;
  }
  
</style>


