<template>
  <div id = "hot">
  	<ul>
  		<li v-for="movie in movies" @click="showDetail(movie.title, $event)">
  			<img :src="movie.poster">
  			<p class="movietitle">{{movie.title}}</p>
<!--   			<p>主演：{{movie.actors.join(" ")}}</p>
  			<p class="intro">简介:{{movie.introduction}}</p> -->
        <button @click="buyTicket(movie.title)" class="btn buy_ticket">购票</button>
  		</li>
  	</ul>
  </div>
</template>

<script>
export default {
  name: 'hot',
  created: function(){
    this.getMovies();
  },
  data() {
    return {
      movies: [{
      	poster: require('../assets/poster/poster1.jpg'),
      	title: "海边的曼彻斯特",
      	// actors: ["卡西·阿弗莱克", "米歇尔·威廉姆斯"],
      	// introduction: "..."
      }
      ]
    }
  },
  methods: {
    getMovies() {
      this.$http.get('/movie/hot')
      .then(res => {
        console.log("获取成功");
        this.movies = res.data;

        this.movies.forEach(function(item){
          //let context = "../assets/poster/";
          let posUrl = item.poster;
          item.poster = require("../assets/poster/" + posUrl + ".jpg");
        });
      })
      .catch(e => {
        console.log("服务器异常！");
        console.log(e);
      })
    },
    buyTicket(title) {
      this.$router.push(`/cinemas/${title}`);
    },
    showDetail(title, event) {
      if (event.target.tagName.toLowerCase() === "button") {
        this.buyTicket(title);
      } else {
        this.$router.push(`/movie/detail/${title}`);
      }
    }
  }
}
</script>

<style scoped>
img {
	width: 10em;
}
ul {
	list-style-type: none;
  display: flex;
  justify-content: flex-start;
  flex-wrap: wrap;
}

li {
  position: relative;
	border: 1px #ffff8c solid;
  padding: 20px;
  text-align: center;
  margin:10px;
  width: 220px;
  height: 300px;
}

.buy_ticket {
  position: absolute;
  right: 5px;
  bottom: 5px;
}

.movietitle {
  font-family: SimHei;
}
</style>


