<template>
  <div id="app">
  	<header>
  		<div id="logo">
  			<img id = "icon" src = "./assets/icon.jpg">
	  		<span>BeeBee Movie</span>
	  	</div>
	  	<div v-if="login" class="greeting">
			<span>欢迎回来，{{getuser}}！</span>
			<button @click="logout" class="btn">退出登录</button>
	    </div>
	    <div v-else class="greeting">
	    	<router-link to="/welcome/signin">登陆</router-link> | 
    		<router-link to="/welcome/signup">注册</router-link>
	    </div>
	  		
  	</header>
    <router-view></router-view>
  </div>
</template>

<script>
export default {
  name: 'app',
  data() {
  	return {
  		username:" ",
      login: document.cookie.length
  	}
  },
  computed: {
    getuser: function(){
      if(document.cookie.length == 0) return "";
      return this.$cookie.get("user");
    }
  },
  methods: {
    logout: function() {
      this.$http.get('/user/logout')
      .then(res => {
          console.log(res.data);
          if (document.cookie.length == 0) {
            this.$router.push('/welcome');
          }
        })
      .catch(e => {
        console.log("服务器异常！");
        console.log(e)
      })
    },
    login: function() {
    	this.$router.push('/welcome/signup');
    }
  }
}

</script>

<style>

#app {
  font-family: Microsoft Yahei, Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: grey;
}

header {
	height: 36px;
	display: flex;
	justify-content: space-between;
	align-items: center;
	border-bottom: 2px #ffff8c solid;
}
#icon {
    height: 30px;
    width: 30px;
}
#logo *{
	vertical-align: middle;
}
.greeting {
    display: flex;
    align-items: center;
  }
.btn {
	background-color: white;
	border: dotted 3px #ffff8c;
	border-radius:5px;
	word-break:keep-all;           /* 不换行 */
	white-space:nowrap; 
	color: #ff8585;
}

.btn:hover {
	color: yellow;
	cursor:pointer
}
</style>



