<template>
  <div id = "movies">
    
    <nav>
      <router-link to="/movies/hot">正在热映</router-link>
      <router-link to="/movies/coming">即将上映</router-link>
      <router-link to="/movies/guess" v-if="login">猜你喜欢</router-link>
    </nav>
    <router-view></router-view>
  </div>
</template>


<script>

export default {
  name: 'movies',
  data() {
  	return {
  		username:" ",
      login: document.cookie.length
  	}
  },
  computed: {
    getuser: function(){
      if(document.cookie.length == 0) return "";
      return this.$cookie.get("user");
    }
  },
  methods: {
    logout: function() {
      this.$http.get('/api/logout')
      .then(res => {
          console.log(res.data);
          if (document.cookie.length == 0) {
            this.$router.push('/welcome');
          }
        })
      .catch(e => {
        console.log("服务器异常！");
        console.log(e)
      })
    }
  }
}

</script>

<style scoped>
  nav {
    display: flex;
    display: -webkit-flex;
    justify-content:space-around;
    background-color: #ffff8c;
    height: 40px;
    align-items: center;
  }
  

  a {
    text-decoration:none;
    color: #b9b9b9;
  }

  a:visited {
    color: #b9b9b9;
  }

  a:hover {
    color: white;
  }
</style>


