<template>
  <div id = "signup">
    <p class="em">{{errMsg}}</p>
    <form @submit.prevent="submit">
      <div class="line">
        <label>用户名：</label>
        <input type="text" name="username" class="username" v-model="user.username" placeholder="username" @blur="check('用户名不能为空',$event)">
      </div>
      <div class="line">
        <label>密码：</label>
        <input type="password" name="password" class="password" v-model="user.password" placeholder="password" @blur="check('密码不能为空',$event)">
      </div>
      <div class="line">
        <label>重复密码：</label>
        <input type="password" name="repeated" class="repeated-password" placeholder="repeat password" @blur="check('两次密码不一致',$event)">
      </div>
      <div class="line">
        <label>喜好：</label>
        <div class="per">
          <input type="checkbox" id="Romance" value="Romance" v-model="user.pereference">
          <label for="Romance">爱情</label>
          <input type="checkbox" id="Comedy" value="Comedy" v-model="user.pereference">
          <label for="Comedy">喜剧</label>
          <input type="checkbox" id="Adventure" value="Adventure" v-model="user.pereference">
          <label for="Adventure">冒险</label>
          <input type="checkbox" id="Horror" value="Horror" v-model="user.pereference">
          <label for="Horror">恐怖</label>
          <input type="checkbox" id="Action" value="Action" v-model="user.pereference">
          <label for="Action">动作</label>
          <input type="checkbox" id="Others" value="Others" v-model="user.pereference">
          <label for="Others">其它</label>
        </div>
      </div>
      <input class="btn" type="submit" value="提交">
    </form> 
  </div>
</template>

<script>
export default {
  name: 'signup',
  data() {
    return {
      user: {
        username: '',
        password: '',
        pereference: []
      },
      errMsg: ''
    }
  },
  methods: {
    submit: function() {
        if(!this.checkForm()) return;
        else {
          var formData = JSON.stringify(this.user);
          this.$http.post('/user/signup', formData)
          .then(res => {
              console.log(res.data)
              console.log('注册成功');
              if(res.data = '用户已存在') {
                this.errMsg = res.data;
              } else if (document.cookie.length > 0) {
                this.$router.push('/movies');
              }
            })
            .catch(e => {
              console.log("服务器异常！");
              console.log(e)
            })
        }
        
    },
    check: function(msg, e) {
      if(e.target.name === 'username' || e.target.name ==='password') {
        if (e.target.value === '') {
          this.errMsg = msg;
        } else {
          this.errMsg = '';
        }
      } else {
        if (e.target.value !=  document.querySelector(".password").value) {
          this.errMsg = msg;
        } else {
          this.errMsg = '';
        }
      }
    },
    checkForm: function() {
      if (document.querySelector(".username").value === '') {
        this.errMsg = '用户名不能为空';
        return false;
      } else if (document.querySelector(".password").value === '') {
        this.errMsg = '密码不能为空';
        return false;
      } else if(document.querySelector(".password").value != document.querySelector(".repeated-password").value) {
        this.errMsg = '两次密码不一致';
        return false;
      }
      return true;
    }
  }
}
</script>

<style scoped>
#signup {
  width: 600px;
}
.em {
  text-align: right;
  margin-right: 10px;
  color: red;
}
form {
  margin-top: 10px;
  margin-left: 345px;
}
label {
  text-align: right;
}
.line {
  margin-bottom: 10px;
  display: flex;
  justify-content:flex-end;
}
.per {
  width: 173px;
  display: flex;
  flex-direction:row;
  justify-content: space-around;
  flex-wrap:wrap;
}
</style>


